import { AppTFEnv } from '..';

import { MouseEventData } from '../Model/AppCoreWrapper/IOWrapper/EventInteraction/MouseInteraction';

export class Implementation {
    app: AppTFEnv;

    // tslint:disable-next-line:no-any
    tfProject: any;
    // tslint:disable-next-line:no-any
    SceneVisMapArchGIS: any;

    constructor(app: AppTFEnv) {
        this.app = app;
        this.InitProjects();
    }
    async InitProjects() {
        return import('./TFProject').then(({ TFProject }) => {
            this.tfProject = new TFProject(this);
        }).catch(err => {
            console.log('Stop loading NCanvas implementation, TFProject.'); console.log(err);
        });
    }
    // .............................................................................................................
    MouseEventController(e: MouseEventData) {
        if (this.tfProject) { this.tfProject.activeCommandController.MouseEventController(e); }
    }
    Render(ctx: CanvasRenderingContext2D) {
        if (this.tfProject) { this.tfProject.Render(ctx); }
    }
    RemoveAllScene() {
        if (this.tfProject) { this.tfProject.Dispose(); } this.tfProject = undefined;
    }
    ShiftScene(v: string) {
        // this.RemoveAllScene();
        // if (v === 'canvasVisualization') {
        //     this.InitSceneVis();
        // }
    }
    LoadDefinition(v0: string) {
        if (this.tfProject) { this.tfProject.LoadDefinition(v0); }
    }
}