
import { ViewAppCoreWrapper } from './ViewAppCoreWrapper';
import { ViewAssetWrapper } from './ViewAssetWrapper';
import { ViewControllerWrapper } from './ViewControllerWrapper';
// import { ViewUIWrapper } from './ViewUIWrapper';

import { NUIWrapper } from './NUIWrapper';

import { AppTFEnv } from '..';
export class ViewController {
    app: AppTFEnv;

    viewAppCoreWrapper: ViewAppCoreWrapper;
    viewAssetWrapper: ViewAssetWrapper;
    viewControllerWrapper: ViewControllerWrapper;
    // viewUIWrapper: ViewUIWrapper;

    NUIWrapper: NUIWrapper;

    constructor(app: AppTFEnv, needUI: boolean) {
        this.app = app;

        this.viewAssetWrapper = new ViewAssetWrapper(this);
        this.viewAppCoreWrapper = new ViewAppCoreWrapper(this);

        this.viewControllerWrapper = new ViewControllerWrapper(this);

        console.log('00')
        this.NUIWrapper = new NUIWrapper(this, needUI);
        console.log('10')
    }
    Resize(width: number, height: number) {
        this.viewControllerWrapper.viewActiveCommandController.Resize(width, height);
    }
    Destroy() {
        this.NUIWrapper.Destroy();
    }
}